@charset "UTF-8";
.biz-wrapper[data-v-b95ed3b4] {
  float: left;
  position: relative;
  margin-top: 9px;
  width: 200px;
  color: #979ba5;
  font-size: 14px;
}
.current-biz-name[data-v-b95ed3b4] {
  position: relative;
  padding: 0 20px 0 10px;
  height: 32px;
  line-height: 32px;
  border: 1px solid #445060;
  border-radius: 2px;
  cursor: pointer;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.current-biz-name[data-v-b95ed3b4]:after {
    content: '';
    position: absolute;
    top: 12px;
    right: 10px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 8px 6px 0 6px;
    border-color: #63656e transparent transparent transparent;
    -webkit-transition: -webkit-transform 0.3s ease-in-out;
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}
.current-biz-name[data-v-b95ed3b4]:hover {
    color: #ffffff;
    border-color: #616d7d;
}
.current-biz-name.list-open[data-v-b95ed3b4]:after {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    -webkit-transform-origin: center;
            transform-origin: center;
}
.disabled-biz-name[data-v-b95ed3b4] {
  cursor: not-allowed;
}
.disabled-biz-name[data-v-b95ed3b4]:hover {
    color: #979ba5;
    border-color: #445060;
}

