.common-section-title {
  position: relative;
  margin: 0;
  padding: 0 0 0 10px;
  line-height: 1;
  font-size: 16px;
  font-weight: 600;
  color: #666666;
}
.common-section-title:before {
    content: '';
    display: inline-block;
    position: absolute;
    top: -2px;
    left: 0;
    width: 2px;
    height: 20px;
    background: #a3c5fd;
}
.common-form-item {
  margin-bottom: 20px;
}
.common-form-item::after {
    display: block;
    content: "";
    clear: both;
}
.common-form-item:last-child {
    margin-bottom: 0;
}
.common-form-item > label {
    position: relative;
    float: left;
    margin-top: 6px;
    width: 100px;
    font-size: 14px;
    font-weight: bold;
    color: #666666;
    text-align: right;
}
.common-form-item > label.required:after {
      content: '*';
      position: absolute;
      top: 0px;
      right: -10px;
      color: #ff2602;
      font-family: "SimSun";
}
.common-form-item .common-form-content {
    margin-left: 120px;
    min-height: 36px;
}
.common-dialog .bk-dialog .bk-dialog-tool {
  min-height: 20px;
}
.common-dialog .bk-dialog .bk-dialog-header {
  padding: 16px 20px 16px;
  border-bottom: 1px solid #ebebeb;
}
.common-dialog .bk-dialog .bk-dialog-header .bk-dialog-header-inner {
    font-size: 20px;
}
.common-dialog .bk-dialog .bk-dialog-body {
  padding: 0;
}
.common-error-tip {
  display: inline-block;
  font-size: 12px;
  line-height: 1;
  color: #ff5757;
}
.common-warning-tip {
  display: inline-block;
  font-size: 14px;
  color: #ffb400;
}
.panagation {
  padding: 10px 20px;
  text-align: right;
  border: 1px solid #dde4eb;
  border-top: none;
  background: #ffffff;
  overflow: hidden;
}
.panagation .page-info {
    float: left;
    line-height: 36px;
    font-size: 12px;
}
.bk-select.is-focus {
  box-shadow: none !important;
  -webkit-box-shadow: none !important;
}
body {
  color: #333333;
  background: #f4f7fa;
  font-family: 'Microsoft YaHei','PingFang SC','Hiragino Sans GB','SimSun','sans-serif';
}
.clearfix::after {
  display: block;
  content: "";
  clear: both;
}
html, body {
  height: 100%;
}
#app {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  min-width: 1320px;
}
.main-container {
  width: 100%;
  height: calc(100% - 50px);
  min-width: 1320px;
  min-height: calc(100% - 50px);
  overflow-x: hidden;
}
